/**
 * @param {string|array} keySequence Could be a string with single key, or an array of sequence of keys in proper order...
 */
export default (keySequence, obj) => {
  return props => {
    const keys = typeof keySequence === 'string' ? [keySequence] : keySequence;
    try {
      const value = keys.reduce((_object, key) => _object[key], props);
      return obj[value];
    } catch (e) {
      console.log(e);
      return {};
    }
  };
};
